import Vue from 'vue'
import Router from 'vue-router'
const explain = r => require.ensure([], () => r(require('../page/explain')), 'explain')
const adminSet = r => require.ensure([], () => r(require('../page/adminSet')), 'adminSet')
const home = r => require.ensure([], () => r(require('../page/home')), 'home')
const login = r => require.ensure([], () => r(require('../page/login')), 'login')
const manage = r => require.ensure([], () => r(require('../page/manage')), 'manage')
const transactionRecoveryInfo = r => require.ensure([], () => r(require('../page/transactionRecoveryInfo')), 'transactionRecoveryInfo')
const transactionGroupStatusList = r => require.ensure([], () => r(require('../page/transactionGroupStatusList')), 'transactionGroupStatusList')

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            component: login
        },
        {
            path: '/manage',
            component: manage,
            name: '',
            children: [{
                path: '',
                component: home,
                meta: [],
            }, {
                path: '/transactionRecoveryInfo',
                component: transactionRecoveryInfo,
                meta: ['', ''],
            }, {
                path: '/transactionGroupStatusList',
                component: transactionGroupStatusList,
                meta: ['', ''],
            },/* {
                path: '/adminSet',
                component: adminSet,
                meta: ['', ''],
            },*/  {
                path: '/explain',
                component: explain,
                meta: ['', ''],
            }]
        }
    ]
})



